import React ,{useEffect} from 'react'
import {  Row,Col ,Button, ListGroup, Image,Card} from "react-bootstrap";
import {useDispatch, useSelector} from 'react-redux'
import { useNavigate,  } from 'react-router-dom';
import Message from '../components/Message'
import CheckOutSteps from '../components/CheckOutSteps'
import { Link } from 'react-router-dom';
import {creatOrder}from '../actions/orderActions'
import {ORDER_CREATE_RESET}from '../constants/orderConstans'



const PlaceOrderScreen = () => {
  const orderCreate=useSelector(state=> state.orderCreate)
  const{order,error,success}= orderCreate
 
 
  const dispatch=useDispatch()

  const navigate = useNavigate()
  const cart=useSelector(state => state.cart)
  //objects only for this page not from state
  cart.itemsPrice = cart.cartItems.reduce((acc,item)=> acc + item.price *item.qty , 0 ).toFixed(2)
  // The shipping price depends on the total price of the items
  cart.shippingPrice = (cart.itemsPrice > 300? 0 : 10).toFixed(2)
  cart.taxPrice = Number((0.17) * cart.itemsPrice).toFixed(2)
  cart.totalPrice = (Number(cart.itemsPrice) +Number(cart.shippingPrice))
  
  if(!cart.paymentMethod  ){
    navigate(`/payment`)

  }


  useEffect( ( ) => {
    if (success){
      navigate(`/order/${order._id}`)
      dispatch({type:ORDER_CREATE_RESET})
    }

  
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [navigate, success])
  

  const placeOrder = ()=>{
    dispatch(creatOrder({
      orderItems: cart.cartItems,
      shippingAddress:cart.shippingAddress,
      paymentMethod: cart.paymentMethod,
      itemsPrice: cart.itemsPrice,
      shippingPrice: cart.shippingPrice,
      taxPrice: cart.taxPrice,
      totalPrice: cart.totalPrice,

    }))
  }
  return (
    <div>
      <CheckOutSteps step1 step2 step3 step4 />
      <Row>
        <Col md={8}>
            <ListGroup variant='flush'>
                <h2> Shipping</h2>
                
                <p>
                    <strong> Shipping : </strong>
                    {cart.shippingAddress.address},{cart.shippingAddress.city}
                    {'  '}
                    {cart.shippingAddress.postalCode}
                    {'  '}   
                    {cart.shippingAddress.country}
              </p>

            </ListGroup>

            <ListGroup variant='flush'>
                <h2> Payment Method</h2>
                <p>
                    <strong> Method : </strong>
                    {cart.paymentMethod}
              </p>

            </ListGroup>
            
            <ListGroup variant='flush'>
                <h2> Order Items</h2>
                {cart.cartItems.length === 0 ? <Message variant='info'>Your cart is Empty</Message> :(
                <ListGroup variant='flush'>
                  {cart.cartItems.map((item, index)=>(
                    <ListGroup.Item key={index}>
                      <Row>
                        <Col md={1}>
                        <Image   src={item.image} alt={item.name} fluid rounded></Image>
                        </Col>  
                        <Col>
                        <Link to={`/product/${item.product}`} >{item.name}</Link>
                        </Col>
                        <Col md={4}>
                          {item.qty} X ₪{item.price} = ₪ {(item.qty * item.price).toFixed(2)}
                        </Col>
                      
                      </Row>



                    </ListGroup.Item>
                  ))}

                </ListGroup >
                )}
               
            </ListGroup>
        </Col>
        <Col md={4}>
            <Card>
                    <ListGroup variant='flush'>
                      <ListGroup.Item>
                        <h2>Order Summary</h2>
                      </ListGroup.Item>
                      <ListGroup.Item>
                          <Row>
                            <Col >Items:</Col>  
                            <Col>₪{cart.itemsPrice}(tax includ)</Col>
                          </Row>
                      </ListGroup.Item>
                      <ListGroup.Item>
                          <Row>
                            <Col >Shipping:</Col>  
                            <Col>₪{cart.shippingPrice}</Col>
                          </Row>
                          </ListGroup.Item>

                          <ListGroup.Item>
                          <Row>
                            <Col >Tax:</Col>  
                            <Col>₪{cart.taxPrice}</Col>
                          </Row>
                          </ListGroup.Item>

                          <ListGroup.Item>
                          <Row>
                            <Col >Total:</Col>  
                            <Col>₪{cart.totalPrice}</Col>
                          </Row>
                      </ListGroup.Item>
                      <ListGroup.Item>
                        {error && <Message variant='danger'> {error}</Message>}
                      </ListGroup.Item>
                      
                      <ListGroup.Item>
                          <Button
                          type='button'
                          className='btn-clock'
                          disabled ={cart.cartItems === 0}
                          onClick ={placeOrder}
                          >
                            Place Order
                          </Button>
                      </ListGroup.Item>


                    </ListGroup>
            </Card>
        </Col>
      </Row>
    </div>
  )
}

export default PlaceOrderScreen
